
h2{
    margin:  20px 8px;
    color: rgb(100, 100, 100);
    font-size: 24px;
}

.row{
    width: 100%;

    display: flex;
    align-items: flex-end;
    gap: 10px;
    margin: 15px 10px;
}
.coll{
    position: relative;
    height: 100%;
    width: 100%;
    min-height: 2rem;
}

label{
    color: grey;
    font-size: 0.9em;
}

.input{
    width: 100%;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid rgb(206, 212, 218);
    padding: 7px 6px;
}

.input:focus {
    border-radius: 4px;
    border: 1px solid rgb(128, 189, 255);
}

.input:hover {
    border-radius: 4px;
    border: 1px solid rgb(128, 189, 255);
}

.input:focus-visible {
    border-radius: 4px;
    border: 1px solid rgb(128, 189, 255);
    outline-color: none;
}

button {
    background-color: transparent;

    cursor: pointer;
    font-weight: 500;
 
    text-transform: uppercase;

    padding: 10px 0px;
    border-radius: 4px;
    border: 1px solid rgba(25, 118, 210, 0.5);
    color: rgb(25, 118, 210);
    transition: 0.1s;
    

    bottom: 0;
    width: 100%;
    overflow: hidden;
}

button:hover {
    text-decoration: none;
    background-color: rgba(25, 118, 210, 0.04);
    border: 1px solid rgb(25, 118, 210);
    transition: 0.1s;
}

button:active {
    box-shadow: 
    rgb(0 0 0 / 12%) 0px 3px 14px 2px,
        rgb(0 0 0 / 14%) 0px 8px 10px 1px, 
        rgb(0 0 0 / 12%) 0px 3px 14px 2px;
    transition: 0.1s;
}

.params{
    width: 100%;
    position: absolute;
    bottom: 0;
    font-size: 1rem;
    border-bottom: 1px solid rgb(206, 212, 218);
    padding: 8px 6px;

}
