*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.matrix-grid{
    width: 100%;

    display: grid;
    grid-template-columns: 30px repeat(5, 2fr);
    grid-template-rows: repeat(5, 100px) 30px;
    border: 1px dotted #555;
}

.likelihood{
    position: relative;
    grid-row: span 6;
    align-self: end;
    font-size: 16pt;
}

.likelihood>span{
    position:absolute;
    transform:rotate(-90deg);
    margin-left: -46px;
    bottom: 75px;
    font-size: 16pt;
}
.criticality{
    grid-column-start: 2;
    grid-column: 5 span;
    font-size: 16pt;
    
}

.item{
    display: flex;
    position: relative;
    border: 1px dotted  #ddd;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.item:hover::before{
    content: ".";
    font-size: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.06);
    position: absolute;
}

.item:active{
    z-index: 1;
    box-shadow: 
    rgb(0 0 0 / 12%) 0px 3px 14px 2px,
        rgb(0 0 0 / 14%) 0px 8px 10px 1px, 
        rgb(0 0 0 / 12%) 0px 3px 14px 2px;
    transition: 0.1s;
}

.wraper{
    display: grid;
    grid-template-columns: 3fr 1fr;
}

ol{
    padding: 0px 2em;
}